.aside{
    position: fixed;
    background: #fff;
    width: 90%;
    height: 100%;
    transition: all 0.4s;
    padding: 15px;
    top: 0;

    
}

.open{
    left: 10%;
}

.close{
    left: 100%;
}

@media (min-width: 980px) {
    .aside{
        position: relative;
        left: 0;
        width: 100%;
    }
  }