.base{
    display: flex;
    flex-direction: column;
}

@media (min-width: 980px) {
    .base{
        display: flex;
        flex-direction: row;
        max-width: 1200px;
        margin: auto;
    }
    .base section:first-child{
        width: 30%
    }

    .base section:second-child{
        width: 70%
    }
}
